.products-row {
  display: flex;
  align-items: center;
  gap: 10px;
}


.arrowing {
  width: 22px;
  height: 22px;
  flex-shrink: 0;        
  cursor: pointer;
}

.products {
  display: flex;
  gap: 12px;
  flex: 1;
}


.product-card {
  flex: 1;
  border: 1.5px solid #ccc;
  border-radius: 14px;
  background: #fff;
  overflow: hidden;
}


.img-box {
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #e0e0e0;
}

.img-box img {
  max-width: 80%;
  max-height: 80%;
  object-fit: contain;
}


.info {
  padding: 10px;
  text-align: center;
}

.info h4 {
  font-size: 14px;
  margin-bottom: 4px;
}

.item {
  font-size: 11px;
  color: #666;
}

.price {
  font-size: 16px;
  color: #0066ff;
  font-weight: bold;
  margin: 6px 0;
}

.qty {
  width: 100%;
  padding: 6px;
  font-size: 12px;
  border-radius: 8px;
  border: 1px solid #ccc;
  margin-bottom: 6px;
  background: #fff;
}

.cart {
  width: 100%;
  padding: 8px;
  font-size: 13px;
  border-radius: 20px;
  border: none;
  background: #0066ff;
  color: #fff;
}


