* {

    padding: 0;
    margin: 0;
    box-sizing: border-box;
}



.body{

    padding: 0;
    margin: 0;
        font-family: sans-serif;
   
    font-family: 'Inter', 'Roboto', sans-serif;

}



.parts-card {
  max-width: 420px;
  padding: 20px;
  border-radius: 12px;
  background: #f7f9fc;
  font-family: Arial, sans-serif;
}


.parts-row {
  display: flex;
  align-items: center;
  gap: 14px;
  margin-bottom: 16px;
}


.parts-icon {
  width: 46px;
  height: 46px;
  object-fit: contain;
  flex-shrink: 0;
}

.parts-text {
  font-size: 15px;
  color: #333;
  line-height: 1.4;
  margin: 0;
}

.parts-btn {
  background-color: #1a73e8;
  color: #fff;
  border: none;
  padding: 12px 28px;
  font-size: 15px;
  border-radius: 24px;
  cursor: pointer;
  font-weight: 500;
  margin-left: 60px;
}

.parts-btn:hover {
  background-color: #1558b0;
}

  


accessss{

  border: 2px solid royalblue;
}


.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: #fff;
  padding: 20px 30px;
  border-radius: 10px;
  text-align: center;
  max-width: 400px;
  width: 90%;
}

.modal-close {
  margin-top: 20px;
  padding: 10px 20px;
  cursor: pointer;
}




.products {
  display: flex;
  gap: 10px;
  overflow-x: auto;
  scroll-behavior: smooth;
  padding: 10px 0;
}

.products::-webkit-scrollbar {
  display: none; /* hide scrollbar */
}

.arrowing {
  width: 30px;
  cursor: pointer;
  user-select: none;
}



